.App{

}

.products{
 margin: 20px;
 padding: 0;
 display: grid;
 gap: 20px;
 grid-template-columns: 1fr 1fr 1fr;
 
}

.productit{  
  
  padding: 20px;
  
  height: 200px;
  border-radius: 5px;
  text-align: center;
  background-color: gray;
  cursor: pointer;

 
}

.productit > img{
  height: 95%;
  width: 100%;
  object-fit: cover;

}

.pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}
.pagination > span{
  
  padding: 20px;
  border: 1px solid;
  cursor: pointer;
}
.pagination-selected{
  background-color: gray;
}
.nextbtn-disable{
  opacity: 0;
}
.prvbtn-disable{
  opacity: 0;
}